import { put, takeLatest } from 'redux-saga/effects';
import { loaderAction } from './../../loader/loader.action';
import { APIcall } from '../../../services/service';
import environment from './../../../index';
import { commonAction } from '../../commonActivity/action';
import { TemplateListActions } from '../../TemplateList/action';

const { setLoader, removeLoader } = loaderAction;

export const ManageCreateTurnTemplate = {
  postNewTemplate: obj => {
    return { type: 'POST_TEMPLATE_NEW_TEMPLATE', payload: obj };
  },
};

const methodPost = 'POST';

function* addNewTurnTemplate({ payload }) {
  console.log(payload, 'addturntemplate');
  try {
    yield put(setLoader());
    const value = yield APIcall(
      methodPost,
      environment.TurnManagement.CreateNewTurnTemplate,
      payload
    );
    console.log(value, 'turntemplate postcall');
    if (value.status >= 200 && value.status <= 299) {
      yield put(TemplateListActions.getTemplateData());
      yield put(removeLoader());
      commonAction.openAlert({
        title: 'Turn Template',
        content:
          'A New Template' + ' ' + " \' " + payload.turntemplatename + " \' " + ' ' + 'is Created.',
      });
    }
  } catch (e) {
    console.log(e, "message")
    if (e.message.status === 406 ) {
      yield put(removeLoader());
      commonAction.openAlert({
        title: 'Create Turn Template',
        content: 'Please Verify The Template You are Creating.',
      });
    }
    console.log(e.message, "turntemplate post call");
    yield put(removeLoader());
  }
}
export function* CreateTemplateSaga() {
  yield takeLatest('POST_TEMPLATE_NEW_TEMPLATE', addNewTurnTemplate);
}
