import userConstant from './user.constant';

const initialState = {
  isLoggedIn: false,
  isLoadingLogin: false,
  user: {},
  googlelogin:false,
};

export function userReducer(state = initialState, action) {
  const newState = { ...state };
  if (action.type === userConstant.LOGIN_SUCCESS) {
    newState.isLoggedIn = true;
    newState.isLoadingLogin = false;
    newState.user = { ...action.payload };
    return newState;
  }
  if (action.type === userConstant.LOGIN_FAILURE) {
    newState.isLoggedIn = false;
    newState.isLoadingLogin = false;
    return newState;
  }
  if (action.type === userConstant.LOGIN_REQUEST) {
    newState.isLoadingLogin = true;
    return newState;
  }
  if (action.type === userConstant.LOGOUT) {
    newState.isLoggedIn = false;
    newState.user = {};
    return newState;
  }
  // if(action.type === userConstant.LOGINMETHOD){
  //   console.log(action.payload,"loginmethod")
  //   newState.googlelogin = action.payload;
  //   return newState;
  // }
   return state;
}
