import axios from 'axios';
import environment from '..';
import { call } from 'redux-saga/effects';
import { getAuth } from '../auth/auth';

class Service {
  service;
  constructor() {
    const service = axios.create({
      headers: {
        'Content-Type': 'application/json',
      },
    });
    service.interceptors.response.use(this.handleSuccess, this.handleError);

    this.service = service;
  }


  handleSuccess = (response) => {
    const commonAction = require('../Store/commonActivity/action').commonAction;
    const history = require('../Store/store').history;
    const loader = require('../Store/loader/loader.action').default;
    const store = require('../Store/store').default;
    const { setLoader, removeLoader } = loader;
    console.log(response, commonAction, history, 'handlesuccess');
    console.log(store.dispatch, removeLoader, 'storedispatch', loader);
    if (response.status === 401) {
      store.dispatch(removeLoader());
      commonAction.openAlert({
        title: 'Warning',
        content: 'Token Expired Please Login Again.',
      });
      console.log(response.status, "tesssssting");

      history.push('/login');
    } else if (response.status === 404) {
      this.redirectTo(document, '/404');
    } else if (response.status >= 500 && response.status <= 599) {
      store.dispatch(removeLoader());
      commonAction.openAlert({
        title: 'Warning',
        content: 'We Are Facing Server Issue. ',
      });

      // switch (response.data.status) {
      //   case 401:
      //     store.dispatch(removeLoader());
      //     commonAction.openAlert({
      //       title: 'Warning',
      //       content: 'Token Expired Please Login Again... ',
      //     });
      //     history.push('/login');
      //     break;
      //   case 404:
      //     this.redirectTo(document, '/404');
      //     break;
      //   case 500:
      //    store.dispatch(removeLoader());
      //     commonAction.openAlert({
      //       title: 'Warning',
      //       content: 'We Are Facing Server Issue ',
      //     });
      //     break;
      // }
      // return response;
    }
    return response;
  }
  handleError = error => {
    const commonAction = require('../Store/commonActivity/action').commonAction;
    const history = require('../Store/store').history;
    const loader = require('../Store/loader/loader.action').default;
    const store = require('../Store/store').default;
    const { setLoader, removeLoader } = loader;
    console.log(error, commonAction, history, 'testttt');
    console.log(store.dispatch, removeLoader, 'storedispatch', loader);

    if (error.response.status === 401) {
      store.dispatch(removeLoader());
      commonAction.openAlert({
        title: 'Warning',
        content: 'Token Expired Please Login Again.',
      });
      console.log(error.response.status, "tessssstingError");
      history.push('/login');
    } else if (error.response.status === 404) {
      this.redirectTo(document, '/404');
    } else if (error.response.status >= 501 && error.response.status <= 599) {
      store.dispatch(removeLoader());
      commonAction.openAlert({
        title: 'Warning',
        content: 'We Are Facing Server Issue. ',
      });
    }
    return Promise.reject({ message: error.response });
  };

  // redirectTo = (document, path) => {
  //   document.location = path;
  // };
  get = (
    path,
    payload,
    header = {
      'Content-Type': 'application/json',
      'content-security-policy': "default-src 'self' https:; font-src 'self' https: data:; img-src 'self' https: data:; object-src 'none'; script-src https:; style-src 'self' http: 'unsafe-inline'",
      'strict-transport-security': 'max-age=631138519',
      'x-content-type-options': 'nosniff',
      'x-frame-options': 'DENY',
      'x-xss-protection': '1 ; mode=block'
    }

  ) => {
    return this.service
      .request({
        method: 'GET',
        url: path,
        responseType: 'json',
        // data: payload,
        headers: header,
        // validateStatus: false,
      })
      .then(response => {
        return response;
      });
  };

  // get = path => {
  //   return this.service.get(path,{validateStatus:false}).then(response => response);
  // };

  // patch(path, payload, callback) {
  //   return this.service
  //     .request({
  //       method: 'PATCH',
  //       url: path,
  //       responseType: 'json',
  //       data: payload,
  //     })
  //     .then(response => callback(response.status, response.data));
  // }
  post = (
    path,
    payload,
    header = {
      'Content-Type': 'application/json',
      'content-security-policy': "default-src 'self' https:; font-src 'self' https: data:; img-src 'self' https: data:; object-src 'none'; script-src https:; style-src 'self' http: 'unsafe-inline'",
      'strict-transport-security': 'max-age=631138519',
      'x-content-type-options': 'nosniff',
      'x-frame-options': 'DENY',
      'x-xss-protection': '1 ; mode=block'
    }

  ) => {
    return this.service
      .request({
        method: 'POST',
        url: path,
        // responseType: 'json',
        data: payload,
        headers: header,
        //  validateStatus: false,
      })
      .then(response => {
        return response;
      });
  };

  put = (
    path,
    payload,
    header = {
      'Content-Type': 'application/json',
      'content-security-policy': "default-src 'self' https:; font-src 'self' https: data:; img-src 'self' https: data:; object-src 'none'; script-src https:; style-src 'self' https: 'unsafe-inline'",
      'strict-transport-security': 'max-age=631138519',
      'x-content-type-options': 'nosniff',
      'x-frame-options': 'DENY',
      'x-xss-protection': '1 ; mode=block'
    }
  ) => {
    return this.service
      .request({
        method: 'PUT',
        url: path,
        // responseType: 'json',
        data: payload,
        headers: header,
        // validateStatus: false,
      })
      .then(response => response);
  };

  delete = (
    path,
    payload,
    header = {
      'Content-Type': 'application/json',
      'content-security-policy': "default-src 'self' https:; font-src 'self' https: data:; img-src 'self' https: data:; object-src 'none'; script-src https:; style-src 'self' https: 'unsafe-inline'",
      'strict-transport-security': 'max-age=631138519',
      'x-content-type-options': 'nosniff',
      'x-frame-options': 'DENY',
      'x-xss-protection': '1 ; mode=block'
    }
  ) => {
    return this.service
      .request({
        method: 'DELETE',
        url: path,
        // responseType: 'json',
        data: payload,
        headers: header,
      })
      .then(response => response);
  };
  // for saga purpose
  sagaGet = path => {
    console.log(path, 'path');
    return this.service.get(path).then(res => res);
  };
}

