import { put, takeLatest } from 'redux-saga/effects';
import { APIcall } from '../../services/service';
import environment from '../../index';
import userConstant from './user.constant';
import {
  removeAuthorization,
  setAuthorization,
  setAccessToken,
  removeAccessToken,
} from '../../auth/auth';
import { history } from '../store';
import JwtDecode from 'jwt-decode';
import { commonAction } from '../commonActivity/action';

console.log(environment);

export const userActions = {
  login: data => {
    console.log(data, 'tessssting');
    return { type: userConstant.ASYNCLOGIN, data: data };
  },
  logout: () => {
    // console.log(userConstant, 'tessssting1');
    return { type: userConstant.ASYNCLOGOUT };
  },
  // loginmethod: (payload) => {
  //   console.log(payload,"loginmethod")
  //   return { type: userConstant.LOGINMETHOD, payload };
  // }
};

const methodPost = 'POST';

function* login({ data }) {
  console.log(data, "logindata")
  function request(user) {
    return { type: userConstant.LOGIN_REQUEST, user };
  }
  function success(user) {
    return { type: userConstant.LOGIN_SUCCESS, payload: user };
  }
  function failure(error) {
    return { type: userConstant.LOGIN_FAILURE, error };
  }
  removeAuthorization();
  removeAccessToken();
  yield put(request());
  try {
    var queryString = Object.keys(data)
      .map(key => key + '=' + data[key])
      .join('&');

    // console.log(
    //   APIcall('POST', environment.login, queryString, {
    //     'Content-Type': 'application/x-www-form-urlencoded',
    //     Authorization:
    //       'Basic c3ByaW5nLXNlY3VyaXR5LW9hdXRoMi1yZWFkLWNsaWVudDpzcHJpbmctc2VjdXJpdHktb2F1dGgyLXJlYWQtY2xpZW50LXBhc3N3b3JkMTIzNA==',
    //   })
    // );
    // const response = yield call(service.post, environment.Proxyserver, {
    //   method: 'POST',
    //   uri: environment.login,
    //   body: queryString,
    //   headers: {
    //     'Content-Type': 'application/x-www-form-urlencoded',
    //     Authorization:
    //       'Basic c3ByaW5nLXNlY3VyaXR5LW9hdXRoMi1yZWFkLWNsaWVudDpzcHJpbmctc2VjdXJpdHktb2F1dGgyLXJlYWQtY2xpZW50LXBhc3N3b3JkMTIzNA==',
    //   },
    // });

    const headers = {
      'Content-Type': 'application/x-www-form-urlencoded',
      Authorization:
        'Basic c3ByaW5nLXNlY3VyaXR5LW9hdXRoMi1yZWFkLWNsaWVudDpzcHJpbmctc2VjdXJpdHktb2F1dGgyLXJlYWQtY2xpZW50LXBhc3N3b3JkMTIzNA==',
    };
    const response = yield APIcall(
      methodPost,
      environment.login,
      queryString,
      headers
    );

    console.log(response, 'loginresponse');


    //alert('Hello World');
    const decodedValue = JwtDecode(response.data.access_token);
    console.log(decodedValue, "decoded")
    //  if(decodedValue.authorities[0]=== "OCC"){
    let credential = {
      userId: decodedValue.userId,
      access_token: response.data.access_token,
      user_name: decodedValue.user_name,
      expires_in: response.data.expires_in,
      authdata: headers.Authorization,
      loginTime: new Date(),
      refresh_token: response.data.refresh_token,
    };

    yield put(success({ data: credential }));
    // console.log(response, credential, 'login');
    const Pathname = history.location.pathname;
    setAuthorization(credential);
    setAccessToken(credential.access_token);
    history.push('/flightManagement');
    if (
      // Pathname === '/login' ||
      // Pathname === '/flightManagement' ||
      // Pathname === '/dataMaintenance' ||
      // Pathname === '/turnManagement' ||
      // Pathname === '/' ||
      // Pathname === '/redirect' ||
      // Pathname === ''
      response.status === 200
    ) {
      localStorage.setItem('Googlelogin','false')
      history.push('/flightManagement');
    } else {
      history.push(Pathname);
    }
    //  }
    //  else{
    //   function failedremoveloader() {
    //     // console.log('turnnn', payload);
    //     return {
    //       type: userConstant.LOGIN_FAILURE,
    //     };
    //   }
    //   commonAction.openAlert({
    //     title: 'Login',
    //     content: "You Don't have rights to login. Please Contact Admin",
    //   });
    //    yield put(failedremoveloader());
    //   }
  } catch (e) {
    console.log(e.message, 'login responase');
    //  setAuthorization('username');
    // history.push('/flightManagement');
    if (e.message.status > 300 && e.message.status <= 400) {
      // alert('Invalid Credenials');
      commonAction.openAlert({
        title: 'Login',
        content: 'Invalid Login Credentials.',
      });
    } else if (e.message.status === 401) {
      commonAction.openAlert({
        title: 'Login',
        content:
          "User Account is Locked.Please Contact Admin.",
      });
    }
    yield put(failure('login failed'));
  }

}

function* logout() {
  removeAuthorization();
  removeAccessToken();

  yield put({ type: userConstant.LOGOUT });
  history.push('/login');
}

export function* authenticationSaga() {
  yield takeLatest(userConstant.ASYNCLOGIN, login);
  yield takeLatest(userConstant.ASYNCLOGOUT, logout);
}
