import { put, takeLatest } from 'redux-saga/effects';
import { APIcall } from '../../services/service';
import { loaderAction } from './../loader/loader.action';
import environment from './../../index';

const { setLoader, removeLoader } = loaderAction;

export const TemplateListActions = {
  getTemplateData: () => {
    return { type: 'GET_TEMPLATELIST_ASYNC' };
  },
};

const methodGET = 'GET';

function* ManageTemplateData() {
  try {
    yield put(setLoader());
    const value = yield APIcall(
      methodGET,
      environment.TurnManagement.templateAPI
    );
    console.log(value, 'template val');
    if (value.status >= 200 && value.status <= 299) {
      yield put({ type: 'TEMPLATELIST_JSONRESPONSE', json: value });
    }
    yield put(removeLoader());
  } catch (e) {
    console.log(e);
  }
}

export function* actionWatcher() {
  yield takeLatest('GET_TEMPLATELIST_ASYNC', ManageTemplateData);
}
