import React from 'react';
import { NavLink } from 'react-router-dom';
import '../../Page/Menu/Menu.scss';
import { Allimages } from '../../../Utils/imageRetriver';
import config from '../../../config';
import { userActions } from '../../../Store/login/user.action';
import { connect } from 'react-redux';
import { FaPowerOff, FaUserCircle } from 'react-icons/fa';
import { withRouter } from 'react-router';
import { getAuth } from '../../../auth/auth';
import Confirmation from '../../Molecules/confirmation_alert';

class Menu extends React.Component {
  constructor() {
    super();
    this.state = { confirmLog: false };
    this.navArray = [];
  }

  username = () => {
    return getAuth().user_name ;
  };

  renderImg = (location = {}, currentItem = {}) => {
    if (location.pathname === currentItem.path) {
      return currentItem.IconActive;
    }
    return currentItem.Icon;
  };

  handleLogout = () => {
    this.setState({ confirmLog: true })
  };
   deleteAllCookies = () =>{
    var cookies = document.cookie.split(";");
    for (var i = 0; i < cookies.length; i++) {
        var cookie = cookies[i];
        var eqPos = cookie.indexOf("=");
        var name = eqPos > -1 ? cookie.substr(0, eqPos) : cookie;
        document.cookie = name + "=;expires=Thu, 01 Jan 1970 00:00:00 GMT";
    }
    }

  logoutConfirm = () => {
    if( localStorage.getItem('Googlelogin') === 'true'){
      console.log("from sso")
     this.deleteAllCookies();
      // this.props.changegooglelogin(false);
      window.location.href = "https://accounts.google.com/logout?continue=https://redsmart.airasia.com/login";
    
   }else{
    this.props.logout();
    this.setState({ confirmLog: false })
    };
    // window.location.href = "https://accounts.google.com/logout?";
    
    
  }

  cancelLogout = () => {
    this.setState({ confirmLog: false })
  }

  render() {
    const { logo, menuItem, profile } = config;
    const { location } = this.props;
    return (
      <div>
        {this.state.confirmLog ?
          <Confirmation
            title="Logout"
            content="Are you sure you want to Logout the page?"
            deleteId={() => this.logoutConfirm()}
            closeAlert={() => this.cancelLogout()} />
          : ''}

        <div className="sidenav">
          <div className="logo-wrapper" style={{ borderBottom: logo.border }}>
            <div className="logo">
              <img
                className="logo-img"
                alt="logo"
                src={Allimages[logo.img]}
                srcSet={`${Allimages[logo.img2x]}, ${Allimages[logo.img3x]}`}
              />
            </div>
          </div>
          <ul className="sidenavul">
            {menuItem.map(item => (
              <li key={item.path} data-key={item.name}>
                <NavLink to={item.path} activeClassName="nav-active">
                  <div className="active-area-top">
                    <div className="active-circle"></div>
                  </div>
                  <div className="active-area-bottom">
                    <div className="active-circle"></div>
                  </div>
                  <div className="menu-icon">
                    <img src={this.renderImg(location, item)} alt="Icon" />
                  </div>
                  <div className="menu-list">{item.name}</div>
                </NavLink>
              </li>
            ))}
          </ul>
          <div className="profile">
            <div className="user-details">
              <div className="menu-icon">
                <FaUserCircle className="user-logo" />
              </div>
              <div className="menu-list">{this.username()}</div>
            </div>
            <div className="user-logout" onClick={this.handleLogout}>
              <div className="menu-icon">
                <FaPowerOff className="logout-icon" />
              </div>
              <div className="menu-list">{profile.logoutText}</div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

function mapState(state) {
  console.log("loginstate",state)
  const { isLoggedIn } = state.authentication;
  //  let  {googlelogin} = state.authentication;
  return { isLoggedIn };
  
}

const actionCreators = {
  logout: userActions.logout,
  // changegooglelogin:userActions.loginmethod
};

export default withRouter(connect(mapState, actionCreators)(Menu));
