import flightUpdateConstant from './flightUpdate.constant';

const initialState = {
  isError: false,
  updateData: [],
  localData: [],
  updateTurn: [],
  UpdateDone:[]
};

export function fltUpdateReducer(state = initialState, action) {
  const newState = { ...state };
 console.log(action,"reducer action")
  if (action.type === flightUpdateConstant.FLT_UPDATE_SUCCESS) {
    newState.updateData = action.payload;
    return newState;
  }
  if (action.type === flightUpdateConstant.FLT_TURN_FAILURE) {
    newState.updateTurn = action.payload;
    return newState;
  }
  if (action.type === flightUpdateConstant.FLT_UPDATE_STORE_POST_REQUEST) {
    newState.localData = action.payload;
    return newState;
  }
  if (action.type === flightUpdateConstant.FLT_UPDATE_STORE_REQUEST) {
    newState.localData = action.payload;
    return newState;
  }
  if (action.type === flightUpdateConstant.FLT_UPDATE_FAILURE) {
    newState.isError = true;
    return newState;
  }
  if (action.type === flightUpdateConstant.UPDATE_DONE_HANDLER) {
    console.log("reducer1")
    newState.UpdateDone = action.payload;
    console.log(newState,"reducer2")
    return newState;
  }
  return state;
}
