import fltTurnAroundConstant from './flightTurnAround.constant';

const initialState = {
  isError: false,
  turnAroundData: {},
  NextturnisError: false,
  NextturnAroundData: {},
  AddTurndata: {},
};

export function fltTurnAroundReducer(state = initialState, action) {
  const newState = { ...state };

  if (action.type === fltTurnAroundConstant.FLT_MGT_FLIGHTTURNAROUND_SUCCESS) {
    newState.turnAroundData = action.payload;
    console.log('jgh');
    return newState;
  }
  if (action.type === fltTurnAroundConstant.FLT_MGT_FLIGHTTURNAROUND_FAILURE) {
    newState.isError = true;
    return newState;
  }
  if (action.type === fltTurnAroundConstant.FLT_MGT_FLIGHT_NEXT_TURNAROUND_SUCCESS) {
    newState.NextturnAroundData = action.payload;
    return newState;
  }
  if (action.type === fltTurnAroundConstant.FLT_MGT_FLIGHT_NEXT_TURNAROUND_FAILURE) {
    newState.NextturnisError = true;
    return newState;
  }

  if (action.type === fltTurnAroundConstant.FLT_MGT_FLIGHT_ADD_TURNAROUND_SUCCESS) {
    newState.AddTurndata = action.payload;
    return newState;
  }
  if (action.type === fltTurnAroundConstant.FLT_MGT_FLIGHT_ADD_TURNAROUND_FAILURE) {
    newState.AddTurndataError = true;
    return newState;
  }
  if (action.type === fltTurnAroundConstant.FLT_MGT_FLIGHT_TURNAROUND_DEL_SUCCESS) {
    // newState
    console.log(action.payload, "checkna")
    newState.turnAroundData = null;
    return newState
  }
  return state;
}
