import { put, takeLatest, call } from 'redux-saga/effects';
import environment from '../../../index';
import { APIcall } from '../../../services/service';
import { loaderAction } from '../../loader/loader.action';
import { TemplateListActions } from '../../TemplateList/action';
import { commonAction } from '../../commonActivity/action';

const { setLoader, removeLoader } = loaderAction;

export const ViewTemplate = {
  DeleteTask: (val, taskid, tasknm) => {
    console.log(val, taskid, 'valueinaction');
    return {
      type: 'DELETE_TASK_OF_TEMPLATE',
      payload: val,
      task: taskid,
      tasknm: tasknm,
    };
  },
  AddTask: val => {
    return { type: 'ADD_TASK_TO_THE_TEMPLATE', payload: val };
  },
};
const methodPut = 'PUT';

function* AddTask({ payload }) {
  console.log( payload, 'taskfromview response1');
  try {
    yield put(setLoader());
    const value = yield APIcall(
      methodPut,
      environment.TurnManagement.templateAPI + '/' + payload.id,
      payload
    );
    console.log(value, payload, 'taskfromview response');
    if (value.status >= 200 && value.status <= 299) {
      yield put(TemplateListActions.getTemplateData());
      yield put(removeLoader());
      commonAction.openAlert({
        title: 'Edit Turn Template',
        content:  " \' " + payload.turntemplatename + " \' " + 'Updated Successfully',
      });
    }
  } catch (e) {
    if (e.message.status === 406) {
      yield put(removeLoader());
      commonAction.openAlert({
        title: 'Edit Turn Template',
        content:
          " \' " + payload.turntemplatename + " \' " + 'Update Failed.Please Try Again.',
      });
    }
    console.log(e);
    yield put(removeLoader());
  }
}

function* deletetask({ payload, task, tasknm }) {
  try {
    yield put(setLoader());
    const value = yield APIcall(
      methodPut,
      environment.TurnManagement.templateAPI + '/' + payload.turntemplateid,
      payload
    );
    console.log(value, payload, 'deleteresponse');
    if (value.status >= 200 && value.status <= 299) {
      yield put(TemplateListActions.getTemplateData());
      yield put(removeLoader());
      commonAction.openAlert({
        title: 'Edit Turn Template',
        content:
          " \' " + tasknm + " \' " + ' ' + 'is Deleted from' + ' ' + " \' " + payload.turntemplatename + " \' ",
      });
    }
  } catch (e) {
    if (e.message.status === 406) {
      // yield put(TemplateListActions.getTemplateData());
      yield put(removeLoader());
      commonAction.openAlert({
        title: 'Cannot Delete Task',
        content: " \' " + tasknm + " \' " + 'cannot be deleted as it is a dependent task for other turn activities.',
      });
    }
    console.log(e);
    yield put(removeLoader());
  }
}
export function* actionWatcher() {
  yield takeLatest('DELETE_TASK_OF_TEMPLATE', deletetask);
  yield takeLatest('ADD_TASK_TO_THE_TEMPLATE', AddTask);
}
