import { put, takeLatest } from 'redux-saga/effects';
import { APIcall } from '../../services/service';
import environment from './../../index';
import { loaderAction } from './../loader/loader.action';
import { commonAction } from '../commonActivity/action';

const { setLoader, removeLoader } = loaderAction;

export const ManageTaskActions = {
  getTaskDetails: () => {
    return { type: 'GET_TASK_DETAILS_ASYNC' };
  },
  postTaskDetails: obj => {
    return { type: 'POST_TASK_DETAILS', payload: obj };
  },
  deleteTaskDetails: val => {
    return { type: 'DELETE_TASK', payload: val };
  },
};

const methodPost = 'POST';
const methodGET = 'GET';
const methodDelete = 'DELETE';

function* ManageTaskData() {
  try {
    yield put(setLoader());
    const value = yield APIcall(
      methodGET,
      environment.TurnManagement.managetask
    );
    console.log('taskvalue', value);
    if (value.status >= 200 && value.status <= 299) {
      yield put({ type: 'MANAGE_TASK_JSON_RESPONSE', json: value.data });
    }
    yield put(removeLoader());
  } catch (e) {
    console.log(e);
  }
}

function* AddTaskType({ payload }) {
  console.log(payload, 'taskvalueq');
  try {
    yield put(setLoader());
    const value = yield APIcall(
      methodPost,
      environment.TurnManagement.managetask,
      payload
    );
    console.log(value, payload, 'taskvalue');
    if (value.status >= 200 && value.status <= 299) {
      yield put(ManageTaskActions.getTaskDetails());
      yield put(removeLoader());
      commonAction.openAlert({
        title: 'Task',
        content: 'A New Task is Added To The Task List.',
      });
    }
  } catch (e) {
    console.log(e, "tasking");
   if(e.message.status === 406){
      yield put(removeLoader());
      commonAction.openAlert({
        title: 'Department',
        content: 'The Task is Already Present in the list',
      });
     }
    yield put(removeLoader());
  }
}

function* DeleteTask({ payload }) {
  try {
    yield put(setLoader());
    const value = yield APIcall(
      methodDelete,
      environment.TurnManagement.managetask + '/' + payload.id
    );
    console.log(value, 'payloadtask');
    if (value.status >= 200 && value.status <= 299) {
      yield put(ManageTaskActions.getTaskDetails());
      yield put(removeLoader());
      commonAction.openAlert({
        title: 'Task',
        content: payload.id + ' ' + 'is Deleted from Task List.',
      });
    }
  } catch (e) {
    console.log(e);
    yield put(removeLoader());
  }
}

export function* actionWatcher() {
  yield takeLatest('GET_TASK_DETAILS_ASYNC', ManageTaskData);
  yield takeLatest('POST_TASK_DETAILS', AddTaskType);
  yield takeLatest('DELETE_TASK', DeleteTask);
}
