import alertConstant from './alert.constant';
import store from '../../store';
import { put } from 'redux-saga/effects';
//const { dispatch } = store;

const alertContent = { title: 'Alert', content: '' };
const  confirmationmsg= { title: 'Alert', content: '' };

export const alertAction = {
  
  openAlert: (payload = alertContent) =>{
    console.log('hi1')
    store.dispatch({ type: alertConstant.ALERT_OPEN, payload })},
  closeAlert: () => store.dispatch({ type: alertConstant.ALERT_CANCEL }),
  confirmationmsg :(payload = confirmationmsg) =>{
     console.log('hi2')
    store.dispatch({ type: alertConstant.CONFIRMATION_MSG, payload })},
  
};
