import React, { Suspense, lazy } from 'react';
import { Route, Switch, Router,Redirect } from 'react-router-dom';
import Menu from './components/Page/Menu/Menu';
import { PrivateRoute } from './Routing/private-route';
import { history } from './Store/store';
import { InitialLoader } from './Utils/commonActivity/loader';
import CommonActivity from './Utils/commonActivity/index';
import { connect } from 'react-redux';
import { _storage } from './services/localstorage';



//dynamic loading

const LoginPage = lazy(() => import('./components/Page/Login/login'));
const FlightManagement = lazy(() => import('./components/Page/FlightManagement'));
const TurnManagement = lazy(() => import('./components/Page/TurnManagement'));
const Performance = lazy(() => import('./components/Page/Performance'));
const DataMaintenance = lazy(() => import('./components/Page/DataMaintenance'));
const Hub = lazy(() => import('./components/Page/Hub'));
const RedirectComponet = lazy(()=>import ('./components/Page/RedirectComponent'))


export  function App(props) {
  {console.log(history.listen, "historyTest") }
  console.log(props,"appstateprop")

// /console.log(RedirectComponent,"redirectcomponent")
  return (
    
    <Router history={history}>

   { localStorage.getItem('token')  ? <Menu /> : null}
      {/* <Menu /> */}
      <CommonActivity />
      <Suspense fallback={<InitialLoader />}>
        <Switch>
          <Route path="/login" component={LoginPage} />
          <Redirect exact from="/" to="flightManagement" />
          <PrivateRoute path="/dataMaintenance" component={DataMaintenance} />
          <PrivateRoute path="/hub" component={Hub} />
          <PrivateRoute path="/performance" component={Performance} />
          <PrivateRoute path="/flightManagement" component={FlightManagement} />
          <PrivateRoute path="/turnManagement" component={TurnManagement} />
          <Route exact path="/redirect" component={RedirectComponet}/>
           <PrivateRoute exact path="*" component={LoginPage} /> 
          
          
        </Switch>
      </Suspense>
    </Router>
  );
}

function mapState(state) {
  console.log(state,"appstate")
  return { ...state.routing };
}
export default connect(mapState)(App);
